.posts{
    background-color:#fff ;
    padding: 10px;
    margin-top: 10px;
    border-radius: 10px;

}
.posts .post_header{
    display: flex;
    align-items: center;
    justify-content: space-between;


}
.posts .post_header .post_headerLeft{
    display: flex;
    align-items: center;
    


}
.posts .post_header .post_profile_details h3{
    font-size: 14px;
    margin: 0px;
    margin-left: 10px;
}   
.posts .post_header .post_profile_details p{
    font-size: 12px;
    margin: 0px;
    margin-left: 10px;
} 
.post_body p{
    font-size: 14px;
    padding: 20px 0px;
} 
.post_footer{
    display: flex;
    border-top: 1px solid rgb(231,231,231);
    padding-top: 10px;
    margin-top: 20px;

} 
.post_footer .post_footer_option{
    display: flex;
    align-items: center;
    cursor: pointer;
    margin-right: 30px;
}
.post_footer .post_footer_option svg{
   margin-right: 10px;
   color: #333;

}
.post_footer .post_footer_option span{
    margin-right: 10px;
    color: rgb(90, 90, 90);
    font-size: 12px;
 
 }
